<template>
  <div class="banner_box">
    <swiper :options="swiperOption()" class="banner" ref="swiperBanner">
      <swiper-slide v-for="(item, index) in selectedSku.pictureList" :key="index">
        <img class="banner_img" :src="item.enlargedURL" alt="" />  
      </swiper-slide>
      <div v-show="selectedSku.pictureList&&selectedSku.pictureList.length>1" class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <p v-show="!selectedSku.available" class="sold_out">已下架</p>
    <p v-show="selectedSku.isSoldOut&&selectedSku.available" class="sold_out">已售罄</p>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme} from '@/supreme-core'
  import 'swiper/dist/css/swiper.css'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'

  export default {
    name: "swipeBanner",
    props:{
      data:Array
    },
    data(){
      return{
        // swiperOption: {
        //   pagination: {
        //     el: '.swiper-pagination',
        //   },
        //   autoplay: {
        //     delay: 5000,//5秒切换一次
        //   },
        //   loop : true,
        // },
      }
    },
    computed:{
      ...mapState({
        'pageStatus': state => state.detailState.pageStatus,
        'selectedSku': state => state.detailState.data.product.selectedSku
      }),
      imagesArray(){
        var array=[];
        this.selectedSku.pictureList.forEach((pic,ind)=>{
          array.push(pic.enlargedURL);
        });
        return array;
      },
      swiperBanner() {
        return this.$refs.swiperBanner.swiper
      },
    },
    components: {
      swiper,
      swiperSlide
    },
    mounted(){
       // 如果只有一个slide就销毁swiper
      if(this.selectedSku.pictureList && this.selectedSku.pictureList.length<=1){
        this.swiperBanner.destroy();
      }
    },
    methods:{
      swiperOption(){
        let slef = this;
        return{
          pagination: {
            el: '.swiper-pagination',
          },
          autoplay: {
            delay: 5000,//5秒切换一次
            disableOnInteraction: false,//用户操作后不停止自动切换
          },
          on: {
            click: function () {     
              let realIndex = this.realIndex;
              slef.preview(realIndex); 
            }
          },
          loop : true
        }  
      },
      preview(index){
        if(util.isInWebview()){
          // banner埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_Banner_Click',
            currentPage:'MallItemPage_',
            NextPage:'',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          hybrid.showImagesPreview({
            "images": this.imagesArray,
            "index": index
          })
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .banner_box{
    position: relative;
    left: 0;
    top: 0;
  }
  .banner{
    width:100%;
    height: 3.75rem;
  }
  .banner_img{
    display: block;
    width: 100%;
    height: 100%;
  } 
  .sold_out{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color:#FFFFFF;
    font-size: 18px;
    background:rgba(4,11,41,0.4);
    z-index: 2;
  }
</style>



