import {guid ,getQueryString} from '../util/util'
import startModel from './taskStartModel'
import $ from 'webpack-zepto'
/**
 * @class Task
 * @export Task
 * @author lei.you
 * @desc 任务对象
 *
 */
class Task{
  /**
   * @constructor
   * @param {object} {
   *  id: ID
   *  gid: ID
   *  pathName: 页面对应的路径
   *  params:
   *  pageParams: 页面参数
   *  keepAlive：缓存
   *  vue: 对应的vue组件
   *  model：启动模式
   * }
   * */
  constructor(params={}){
    if(params.params){
      this.gid=params.params.url_gid;
    }
    this.pathName;
    this.name;
    this.params;
    this.pageParams;
    this.keepAlive;
    this.vue;
    this.model;
    $.extend(this,params);
    this.id=guid.getGuid();
  }
  /**
   * @desc : task 回退方法
   * */
  back(){
    if(this.vue){
      console.log(this.vue);
      this.vue.backToLastPage();
    }else{
      throw ("task vue is undefined......");
    }
  }

  /**
   * @desc :触发vue 中自定义函数 onAppear
   * */
  appear(){
    if(this.vue){
      this.vue.onAppear();
    }else{
      throw ("task vue is undefined......");
    }
  }
  static model=0;

}
export default Task;



