<template>
  <!-- 文章描述 -->
  <describeBox :content="getDescribeText" :mask="pageStatus.canAutoScroll" />
  <!-- <div ref="describeBox" class="article-describe-box" :style="{borderBottom:haveLine&&'1px solid #F0F1F2'}" v-html="getDescribeText"></div> -->
</template>
<script>
  import { mapState } from 'vuex'
  import describeBox from '@/components/describe.vue'
  // require("../../util/hammer.js")

  export default {
    name: "articleDescribe",
    props:{
      componentKey:{
        type: Number,
        default: 0
      }
    },
    components:{
      describeBox
    },
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data,
        'pageStatus': state => state.articleState.pageStatus,
      }),
      getDescribeText(){
        let DescribeText = '';
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='rich_text'&& item.sortKey==this.componentKey){
            DescribeText = decodeURIComponent(item.data.text);
            // setTimeout(e=>{
            //   this.removeEventImages();
            //   this.addEventImages();
            // })
          }
        })
        return DescribeText;
      },
      haveLine(){
        let data = true;
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='rich_text'&& item.sortKey==this.componentKey){
            data = item.haveLine;
          }
        })
        return data;
      }
    },
    // beforeDestroy(){
    //   this.removeEventImages();
    // },
    methods:{
      // 测试文描图片显示
      // addEventImages:function(){
      //   let doms = this.$refs.describeBox;
      //   let imgs = doms.getElementsByTagName("img");
      //   this.decImagesArr = [];
      //   for(let j=0;j<imgs.length;j++){
      //     imgs[j].dataset.imgIndex=j;
      //     this.decImagesArr.push(imgs[j].src)
      //   }
      //   var imageTap = new Hammer(doms);
      //   this.imageTap=imageTap;
      //   imageTap.on('tap', this.showImages,false);
      //   //doms.addEventListener( 'click', this.showImages, false);
      // },
      // removeEventImages:function(){
      //   let doms = this.$refs.introduce;
      //   if(doms){
      //     doms.removeEventListener('click', this.showImages, false);

      //   }
      //   if(this.imageTap){
      //     this.imageTap.off("tap");
      //   }
      // },
      // showImages(dom){
      //   if(dom.target.nodeName=='IMG' && dom.target.dataset.imgIndex!=null){
      //     hybrid.showImagesPreview({
      //       "images":this.decImagesArr,
      //       "index": parseInt(dom.target.dataset.imgIndex)
      //     })
      //   }else{
      //     console.log('点击的不是图片')
      //   }
      // },
    }
  }
</script>

<style lang="scss" scoped>
  .article-describe-box{
    margin:0 25px;
    padding-bottom: 20px;
    color: #363C54;
    font-size: 14px;
    line-height: 25px;
    letter-spacing:2px;
    overflow: hidden;
  }

</style>



