import {BaseModel,supreme,getCookie} from '@/supreme-core'
import util from '@/util/util';
var envInfo=util.getEnvInfo()
var generateSign = require('@/util/generateSign')
class CommonModel extends BaseModel{
  constructor(config){
    config.timeout=15000;
    //debugger
    config.baseURL = config.url || envInfo.baseUrl + envInfo.application+"/"+ encodeURIComponent(config.api)
    super(config);
  }
  getMobileinfo(){
    let appInfo = JSON.parse(getCookie('appInfo'));
    if(appInfo&&appInfo['mobileinfo']){
      return JSON.stringify(appInfo['mobileinfo']);
    }else{
      return JSON.stringify({ brand:'web',  model:'web', os:'web' });
    }
  }
  post(param){
    if(!param){
      param={};
    }
    /**
    var signObj = generateSign.default()
    param["token"]=supreme.sessionCache.get("token").token;
    param["timestamp"]=signObj.timestamp;
    param["sign"]=signObj.getwaySign;
    param["_sid"]=supreme.sessionCache.get("user")._sid;
     */


    var config = {};
    // if(supreme.sessionCache.get("user") && supreme.sessionCache.get("user").userToken != null){
    if(getCookie('userToken')){
        config.headers={
          "token":getCookie('userToken'),
          "mobileinfo":this.getMobileinfo()
        }
    }else{
      // 本地测试使用 token 中经常会有特殊字符所以特殊情况需要放在localStorage 中才能测试
      if(supreme.getQueryString("token")||sessionStorage.getItem("token")){
        config.headers={
          "token":supreme.getQueryString("token")||sessionStorage.getItem("token"),
          "mobileinfo":this.getMobileinfo()
        }
      }else{
        config.headers={
          "mobileinfo":this.getMobileinfo()
        }
      }
    }
    if(param.Data){
      param.Data=JSON.stringify(param.Data)
    }
    return super.post(param,config);
  }
  get(param){

  }
}
export default CommonModel;



